import React from "react";
import { StyledButton } from "../styled/StyledButton";
import ToggleButton from "../toggle/ToggleButton";

function UnsubFields({onInput}) {
    return <ToggleButton isConfirm renders = {{
        renderButton: onToggle => <StyledButton color = "secondary" onClick = {onToggle}>Dar de Baja</StyledButton>,
        renderCancel: onToggle => <StyledButton onClick = {onToggle}>Cancelar</StyledButton>,
        renderExpanded: () => <StyledButton onClick = {onInput} type = "submit">Confirmar</StyledButton>
    }}/>
}

export default UnsubFields;